body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  align-items: center;
}

ol, ul {
  padding-left: 10px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
  min-width: 150px;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 35px;
  font-weight: bold;
  line-height: 50px;
  height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display:flex;
  flex-basis: 150px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.game-board {
  flex-basis: 150px;
}
.game-info {
  flex-basis: 150px;
  margin: 5px;
}
